<template>

  <router-link class="header__cart" aria-label="Корзина с товарами" :to="{name: 'cart'}">
      <svg width="30" height="21" fill="currentColor">
        <use xlink:href="#icon-cart"></use>
      </svg>
      <div v-if="loadingCartIndicator" class="loader-cart-indicator"></div>
      <span class="header__count" aria-label="Количество товаров" v-else-if="productsAmount">{{ productsAmount }}</span>
    </router-link>

</template>

<script>
import { mapState } from 'vuex';
  export default {
    computed: {
      ...mapState['loadingCartIndicator'],
      productsAmount() {
        return this.$store.state.cartProductsData.length || 0
      },
      loadingCartIndicator() {
        return this.$store.state.loadingCartIndicator
      }
    }
  }
</script>
