<template>

<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        3 товара
      </span>
    </div>
    <section class="loader" v-if="$store.state.loadingCart === true">Загрузка</section>
    <section v-else-if="products" class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BaseFormText title="ФИО" placeholder="Введите ваше полное имя" :error="formError.name" v-model="formData.name"/>
            <BaseFormText title="Адрес доставки" placeholder="Введите ваш адрес" :error="formError.address" v-model="formData.address"/>
            <BaseFormText title="Телефон" placeholder="Введите ваш телефон" :error="formError.phone" v-model="formData.phone"/>
            <BaseFormText title="Email" placeholder="Введи ваш Email" :error="formError.email" v-model="formData.email"/>
            <BaseFormTextarea v-model="formData.comment" title="Комментарий к заказу" :error="formError.comment" placeholder="Ваши пожелания"/>

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked>
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <h3 v-if="products.length <= 0">Корзина пуста</h3>
            <OrderPageCart v-for="item in products" :key="item.product.id" :item="item"/>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ productAmount }}</b> товара на сумму <b>{{ totalPriceCart | numberFormat}} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" style="display:flex;align-items: center;justify-content: center;" type="submit">
            Оформить заказ <span class="loader-send-order" v-show="loader === true"></span>
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>

</template>

<script>
  import { mapGetters } from 'vuex';
  import BaseFormText from '@/components/BaseFormText.vue';
  import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
  import OrderPageCart from '@/components/OrderPageCart.vue';
  import axios from 'axios';
  import { API_BASE_URL } from '@/config';
  import numberFormat from '@/helpers/numberFormat';

  export default {
    components: {
      BaseFormText,
      BaseFormTextarea,
      OrderPageCart
    },
    data() {
      return {
        formData: {},
        formError: {},
        formErrorMessage: '',
        loader: false
      }
    },
    computed: {
      ...mapGetters({products: 'cartDetailProducts'}),
      totalPriceCart() {
        return this.products.reduce((acc, item) => (item.product.price * item.amount) + acc, 0)
      },
      productAmount() {
        return this.products.length
      }
    },
    methods: {
      order() {
        this.formError = {};
        this.formErrorMessage = ''
        this.loader = true
        setTimeout(() => {
          axios
          .post(API_BASE_URL + '/api/orders', {
            ...this.formData
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey
            }
          })
          .then(response => {
            this.$store.commit('resetCart')
            this.$store.commit('updateOrderInfo', response.data)
            this.$router.push({name: 'orderInfo', params: {id: response.data.id}})
          })
          .catch(error => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message
          })
          .then(() => this.loader = false)
        }, 1500)
      }
    },
    filters: {
      numberFormat
    }
  }

</script>
