<template>
  <div>
    <li class="cart__item product" :key="item.productId">

    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info">
      Объем: <span>128GB</span>
    </p>
    <span class="product__code">
      Артикул: {{ item.product.id }}
    </span>

    <BaseCounter v-model.number="amount" />

    <b class="product__price">
      {{ (item.product.price * item.amount) | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProductFromCart(item.productId)">
      <div class="loader-delete" v-if="deleteLoading === true"></div>
      <svg v-else width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
  </div>


</template>

<script>
import BaseCounter from '@/components/BaseCounter.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapMutations, mapActions } from 'vuex';

  export default {
    data() {
      return {
        deleteLoading: false
      }
    },
    props: ['item'],
    components: {
      BaseCounter
    },
    filters: {
      numberFormat
    },
    computed: {
      amount: {
        get() {
          return this.item.amount
        },
        set(value) {
          this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value})
        }
      },
    },
    methods: {
      ...mapMutations({deleteProduct: 'deleteCartProduct'}),
      ...mapActions(['deleteProductToCart']),
      deleteProductFromCart(productId) {
        this.deleteProductToCart(productId)
        this.deleteLoading = true
      }
    }
  }
</script>
